.heading-primary{
	
	position: relative;
	padding-bottom: 0.5rem;
	margin-bottom: 0.5rem;
	font-size:4rem;
	&::before{
		content: "";
		position: absolute;
		bottom: 0rem;
		left: 50%;
		transform: translate(-50%);
		width: 100px;
		height: 3px;
		background: $color-primary;
	}

	

	@media only screen and(max-width: $bp-medium){
      font-size: 2.4rem;

    }

    @media only screen and (max-width: $bp-small){
        font-size: 2rem;
    }
}
.heading-secondary{
	
	
	font-size: 2.4rem;
	font-weight: 300;

}

.heading-tertiary{
	color: $color-dark-1;
	font-size: 2.1rem;
	font-weight: 300;
	line-height: 2.5rem;
	&_light {
		color: $color-light-1;
	}
}

.paragraph{
	font-size: 2.4rem;
	font-weight: 300;
	letter-spacing: 0.36px;
	opacity: 0.7;

	@media only screen and (max-width: $bp-small){
        font-size: 1.6rem;
    }
}

.errorlist{
	background: $color-sell;
	padding: 1rem;
	color: $color-light-1;
	margin: 1rem;
}