
.contact-form{
	background: $color-light-1;
	padding: 17px 10px;
	margin : 0 auto;
	width: 95%;
	
	
	// background: green;
	&__div{
		position: relative;
		width: 100%;
		display: flex;
	}
	&__textarea{
		resize: none;
		background: $color-light-1;
		font-size: 1.5rem;

		padding: 1.5rem 2rem;
		border-radius: 10px;
		border: none;
        border-bottom: 3px solid transparent;
        transition: all $transition-speed;
		width: 100%;
		border: 1px solid gray;
        &:focus {
            outline: none;
            box-shadow: 0 1rem 2rem rgba($color-dark, .1);
            border-bottom: 3px solid $color-primary;
        }
        &:focus:invalid {
            border-bottom: 3px solid $color-secondary-dark;
        }

        &::-webkit-input-placeholder {
            color: $color-primary;
        }
       
        
	}
	&__input{
		background: $color-light-1;
		font-size: 1.5rem;
		padding: 1rem 2rem;
		border-radius: 10px;
		border: none;
        border-bottom: 3px solid transparent;
        transition: all $transition-speed;
		width: 70%;
		border: 1px solid gray;
        &:focus {
            outline: none;
            box-shadow: 0 1rem 2rem rgba($color-dark, .1);
            border-bottom: 3px solid $color-primary;
        }
        &:focus:invalid {
            border-bottom: 3px solid $color-secondary-dark;
        }

        &::-webkit-input-placeholder {
            color: $color-primary;
        }

        @media only screen and (max-width: $bp-large){
			font-size: 1.3rem;
			padding: 0.7rem 1rem;
		}
        
        
	}
	&__label{
		display: inline-block;
		padding: 1rem;
		font-size: $default-font-size;
        transition: all $transition-speed;
        @media only screen and (max-width: $bp-large){
			font-size: 1.3rem;
			padding: 0.4rem 0.5rem;
		}
	}

	&__input:placeholder-shown ~ &__label{
		opacity: 0;
        visibility: hidden;
        transform: translateX(-10rem);
	}
	&__textarea:placeholder-shown ~ &__label{
		opacity: 0;
        visibility: hidden;
        transform: translateX(-4rem);

	}

	
	

	&__btn{
		display: block;
		padding: 20px;
	}
}