/* Base */
*{padding: 0px;margin: 0px;border: 0px; box-sizing: inherit;}
*,*:before,*:after{-moz-box-sizing: border-box;-webkit-box-sizing: border-box;box-sizing: border-box;}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
aside,nav,footer,header,section{display: block;}
html,body{height: 100%;margin: 0;padding: 0;min-width: $min-width;position: relative;width: 100%;}
body{line-height: 1.5;font-family: $font-primary;-ms-text-size-adjust: 100%;-moz-text-size-adjust: 100%;-webkit-text-size-adjust: 100%;
	background-color: $color-bg; min-width: 320px; 
	
	-ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */

   
}
body::-webkit-scrollbar {
    display: none;

}
html{
    box-sizing: border-box;
    font-size: 62.5%;
    @media only screen and (max-width: $bp-largest) {
        font-size: 50%;
    }

}



input,button,textarea{font-family: $font-primary;}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none; display:inline-block;}
a:hover{text-decoration: none;}
li{list-style: none;}
img{vertical-align: top; max-width: 100%;}
h1,h2,h3,h4,h5,h6{font-weight: inherit;font-size: inherit;}


::selection {
    background: $color-primary;
    color: $color-light-1;
}
/* /Base */

