.technologies{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	font-size: 1.6rem;
	overflow-y: scroll;
	/* Hide scrollbar for Chrome, Safari and Opera */	
	&::-webkit-scrollbar {
	    display: none;
	}
	/* Hide scrollbar for IE, Edge and Firefox */
	-ms-overflow-style: none;  /* IE and Edge */
  	scrollbar-width: none;  /* Firefox */
	/* .box__item */
	&__item {
		
		overflow: hidden;
		min-height: 100px;
		margin: 2rem;
		padding: 2rem 4rem;
		width: 47px;
		align-items: center;
		text-align: center;
		display:flex;
		flex-direction: column;
		justify-content: space-between;
		position: relative;


		
		// colors
		.fa-html5{
			color: $html-color;
		}
		.fa-css3{
			color: 	$css-color;
		}

		.fa-bootstrap{
			color: black;
		}
		.fa-js{
			color: $js-color;
		}
		.fa-php{
			color: $php-color;
		}
		.fa-sass{
			color: $sass-color;
		}
		.fa-react{
			color: $react-color;
		}
		&::after{
	  		content: "";
			position: absolute;
			display: block;
			background: $color-primary;
			opacity: 0.4;
		    top: 0;
		    left: 0;
		    width: 100%;
		    height: 100%;
			transform: scale(2) translateX(-75%) translateY(-75%) rotate(-28deg);
			transition: transform 1.5s cubic-bezier(0.2, 1, 0.2, 1);
	  	}
	  	
		&:hover::after{
			transform: scale(2) translateX(0) translateY(0) rotate(-28deg);
			cursor: pointer;
		}
	}
	
	&__img{
				
		max-width: 47px;

	}
	&__i{
		font-size: 47px;
		margin-bottom: 0.5rem;
	}
