%btn-shared{
	
	font-size: 2.2rem;

	&:hover{
		cursor: pointer;
		// background: $color-primary;
		// color: set-text-color($color-primary);
	}
}
.btn {
	@extend  %btn-shared;

	&__change-page{
		position: absolute;
		right: 2rem;
		bottom: 1rem;
		color: $color-gray;
		font-weight: 600;
		animation: bounce-left 2s ease infinite;
		display: flex;
		align-items: center;
	}

	&__back-page{
		left: 2rem;
		right: unset;
	}

	&__text{
		margin: 0  1rem;
	}

	&__hidden{
		display: none;
		@media only screen and (max-width: $bp-medium){
			display: flex;
		}
	}

	&__desctop{
		@media only screen and (max-width: $bp-medium){
			display: none;
		}
	}
	&_animated {
		cursor: pointer;
		text-transform: uppercase;
		text-decoration: none;
		padding: 1.3rem 3rem;
		display: inline-block;
		border-radius: 10rem;
		transition:  all 0.2s;
		position: relative;
		font-size: 1.6rem;
		border: none;
		outline:0 none;
		background: $color-primary;
		color: $color-light-1;
		animation: moveInBottom ease-out .75s;
		animation-fill-mode: backwards;
	    &:hover {
  		transform: translateY(-3px);
  		box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  		&::after {
		    transform: scaleX(1.4) scaleY(1.6);
		    opacity: 0; 
		    background: $color-primary;
		}
	  	}
	  	&:active{
			transform: translateY(-1px);
			box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
		}
		&::after{
			content: "";
			display: inline-block;
			height: 100%;
			width: 100%;
			border-radius: 10rem;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
			transition: all .4s;
		} 
	}

}
