@media screen and (orientation: portrait) {
body{display:none} html:before{content:'Please rotate your device';position:fixed;top:0;left:0;right:0;bottom:0;margin:auto;background:#fff;z-index:999;text-align:center;width:80%;height:100px; font-size: 3rem;} 
}

@media screen and (min-width: $bp-medium) {
body{display:block;} 
html:before{
	display: none;
}
}
